import React, { useCallback } from 'react';
import { styles } from './styles';
import { BaseContainer, HeaderBase, Text } from 'tradewize-component';
import { NavigationService } from '../../services/NavigationService';
import { globalStyles } from '../../global/styles';
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import OnboardingProcess from '../OnboardingProcess';
import EnterYourInformation from '../EnterYourInformation';
import TabbarOnboarding, { TAB_ORDER } from '../../components/TabbarOnboarding';
import ShareYourExperience from '../ShareYourExperience';
import WorkReferences from '../WorkReferences';
import UnderstandingYourKnowledge from '../UnderstandingYourKnowledge';
import { tabbarRegisterRef } from '../../helper/register';
import { useDispatch, useSelector } from 'react-redux';
import { clearStateRegisterAsync } from '../../redux/features/register/actions';
import VerifyAccount from '../VerifyAccount';
import colors from '../../theme/colors';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { View } from 'react-native';
import { showSelectedLanguageModal } from '../../redux/features/app/actions';
import { RootState } from '../../redux/store/rootReducer';
import languages from '@cospired/i18n-iso-languages';

const OnboardingTab = createMaterialTopTabNavigator();

const Register = () => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();

  const [activeTab, setActiveTab] = React.useState('OnboardingProcessScreen');
  const language = useSelector((state: RootState) => state.app.language);

  const handlePressTab = React.useCallback((tabName: string) => {
    setActiveTab(tabName);
  }, []);

  const handlePressBack = useCallback(() => {
    const currentIndex = TAB_ORDER.indexOf(activeTab);
    if (currentIndex > 0) {
      // n·∫øu kh√¥ng ·ªü tab ƒë·∫ßu -> quay l·∫°i tab tr∆∞·ªõc ƒë√≥
      const prevTab = TAB_ORDER[currentIndex - 1];
      setActiveTab(prevTab);
      tabbarRegisterRef?.current?.pressPrevious();
    } else {
      // n·∫øu ƒëang ·ªü tab ƒë·∫ßu -> tho√°t ra ngo√†i
      NavigationService.goBack();
      dispatch(clearStateRegisterAsync());
    }
  }, [activeTab, dispatch]);

  const renderTextHeader = React.useCallback(() => {
    switch (activeTab) {
      case 'OnboardingProcessScreen':
        return t('ONBOARDING_PROCESS');
      case 'EnterYourInformationScreen':
        return t('ENTER_YOUR_INFORMATION');
      case 'ShareYourExperienceScreen':
        return t('SHARE_YOUR_EXPERIENCE');
      case 'WorkReferencesScreen':
        return t('WORK_REFERENCES');
      case 'UnderstandingYourKnowledgeScreen':
        return t('UNDERSTANDING_YOUR_KNOWLEDGE');
      case 'VerifyAccountScreen':
        return t('VERIFY_ACCOUNT');
      default:
        return '';
    }
  }, [activeTab, t]);

  return (
    <BaseContainer
      statusBarStyle="dark-content"
      backgroundStatusBarColor={colors.white}
      style={styles.container}
    >
      <HeaderBase
        styleButtonLeft={globalStyles.styleButtonBack}
        onPressBack={handlePressBack}
        titleStyle={styles.titleHeader}
        titleAlign="left"
        title={renderTextHeader()}
        rightActions={
          activeTab === 'EnterYourInformationScreen'
            ? [
                {
                  icon: (
                    <View style={styles.languageButton}>
                      <Text style={styles.languageButtonText}>
                        {languages
                          .getName(language, 'en')
                          ?.slice(0, 3)
                          ?.toUpperCase()}
                      </Text>
                    </View>
                  ),
                  onPress: () => {
                    dispatch(showSelectedLanguageModal());
                  },
                },
              ]
            : undefined
        }
      />
      <OnboardingTab.Navigator
        screenOptions={{
          swipeEnabled: false,
          lazy: true,
        }}
        initialRouteName="OnboardingProcessScreen"
        // eslint-disable-next-line react/no-unstable-nested-components
        tabBar={props => (
          <TabbarOnboarding
            ref={tabbarRegisterRef}
            onTabChange={handlePressTab}
            {...props}
          />
        )}
      >
        <OnboardingTab.Screen
          name="OnboardingProcessScreen"
          component={OnboardingProcess}
        />
        <OnboardingTab.Screen
          name="EnterYourInformationScreen"
          component={EnterYourInformation}
        />
        <OnboardingTab.Screen
          name="ShareYourExperienceScreen"
          component={ShareYourExperience}
        />
        <OnboardingTab.Screen
          name="WorkReferencesScreen"
          component={WorkReferences}
        />
        <OnboardingTab.Screen
          name="UnderstandingYourKnowledgeScreen"
          component={UnderstandingYourKnowledge}
        />
        <OnboardingTab.Screen
          name="VerifyAccountScreen"
          component={VerifyAccount}
        />
      </OnboardingTab.Navigator>
    </BaseContainer>
  );
};

export default Register;
